+component(header-mobile)

  +breakpoint('desktop')
    display: none

  +breakpoint('mobile')
    display: block

  +row-without-margin
  +shadow

  min-height: 6em
  background-color: $color-white

  li li
    font-size: 1em

  +part(categories)
    min-height: 6em

    button
      color: $color-dark-green
      font-size: 2.3em

    nav
      background-color: $color-white

    ul
      list-style-type: none
      padding: 0

    li
      margin: 1.7em

    a
      font-size: 1em
      color: $color-dark-grey

  +part(logo)
    padding-top: 0.7em
    min-height: 6em
    padding-left: 3em

    img
      max-width: 130px

  +part(menu)
    min-height: 6em
    padding-right: 2em
    padding-top: 1.5em

    ul
      list-style-type: none

    a
      font-size: 2.3em

      +option(pink)
        color: $color-pink

      +option(green)
        color: $color-dark-green


